import React, { useState, useEffect } from 'react';
import MainCard from '../../../../ui-component/cards/MainCard';
import ConfirmDialog from '../../../../ui-component/ConfirmDialog/ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { getAllCourse, getCourse } from '../../../../store/actions/CourseActions';
import { withStyles, useTheme } from '@material-ui/styles';
import { useParams } from 'react-router-dom';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';

import { IconSearch } from '@tabler/icons';
import EditIcon from '@material-ui/icons/Edit';
import SubjectIcon from '@material-ui/icons/Subject';
import VisibilityIcon from '@material-ui/icons/Visibility';
import GroupWorkIcon from '@material-ui/icons/GroupWork';
import AddCourse from '../add-course';
import useStyles from '../../../ViewTableStyles';
import useUserRole from '../../../../hooks/useUserRole';
import { searchFocusSkill } from '../../../../services/placeOrderServices';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function AllCourses() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'TEACHER', 'SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const history = useHistory();
    const theme = useTheme();
    const { id } = useParams();
    const [openAddCourses, setOpenAddCourses] = useState(false);
    const [courseId, setCourseId] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });
    const [isSearchCourse, setSearchCourse] = useState('');
    const [isCourseUpdateData, setCourseUpdateData] = useState(null);
    const [focusSkillList, setFocusSkillList] = useState([]);
    const [skillSelected, setSkillSelected] = useState([]);

    const { allModules } = useSelector((state) => state?.course);
    const newData = allModules?.content;
    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleModule = async (data) => {
        setCourseUpdateData(data);
        const val = [];

        await searchFocusSkill().then((res) => {
            const result = res?.data?.content;

            setFocusSkillList(result);

            data?.focusSkillIds.map((itemId) => {
                const newVal = result.find((x) => x.id === itemId);
                const obj = {
                    id: itemId,
                    title: newVal?.title
                };

                val.push(obj);
            });
            setSkillSelected(val);
            setOpenAddCourses(true);
        });
    };

    const handleViewSession = (id) => {
        setCourseId(id);
        history.push(`/admin/course/module/${id}`);
    };

    const handleViewFocusSkill = (id) => {
        setCourseId(id);
        history.push(`/admin/modules/${id}`);
    };

    const handleViewBatch = (id) => {
        history.push(`/admin/module/${id}/batch`);
    };

    const handleSearchCourse = (event) => {
        setSearchCourse(event?.target?.value);
    };

    useEffect(() => {
        dispatch(getAllCourse(id, '', rowsPerPage, isSearchCourse ? isSearchCourse : '', page + 1));
    }, [rowsPerPage, isSearchCourse, page]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                        <span className={classes.pageTitle}>&nbsp; All Modules </span>
                    )}

                    {loggedUserProfile?.role === 'TEACHER' && <span className={classes.pageTitle}>&nbsp; Your Modules </span>}
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid className={classes.titleStyle} xs={12}>
                        <Grid item container>
                            <OutlinedInput
                                className={classes.searchControl}
                                id="input-search-header"
                                onChange={(event) => handleSearchCourse(event)}
                                placeholder="Search"
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </Grid>
                        {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                            <Grid item container style={{ justifyContent: 'flex-end' }}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={() => {
                                        setOpenAddCourses(true);
                                        setCourseUpdateData(null);
                                        setSkillSelected([]);
                                    }}
                                    className={classes.btnAdd}
                                >
                                    Add Module
                                </Button>
                            </Grid>
                        )}
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Title </TableCell>
                                    <TableCell> Description </TableCell>
                                    {loggedUserProfile?.role !== 'TEACHER' && <TableCell align="center"> Price </TableCell>}
                                    <TableCell align="center"> Duration(In Minutes) </TableCell>
                                    <TableCell align="center"> Session Count </TableCell>
                                    <TableCell align="center"> Status </TableCell>
                                    <TableCell align="center"> MinAge </TableCell>
                                    <TableCell align="center"> MaxAge </TableCell>

                                    <TableCell align="right"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {newData?.map((row, index) => (
                                        <StyledTableRow key={index}>
                                            <TableCell> {row?.title} </TableCell>
                                            <TableCell> {row?.description} </TableCell>
                                            {loggedUserProfile?.role !== 'TEACHER' && <TableCell align="center">{row?.price}</TableCell>}
                                            <TableCell align="center"> {row?.duration} </TableCell>
                                            <TableCell align="center"> {row?.sessionsCount} </TableCell>

                                            <TableCell align="center">
                                                <Typography
                                                    className={classes.status}
                                                    style={{
                                                        backgroundColor:
                                                            (row?.status === 'ACTIVE' && theme.palette.success.main) ||
                                                            (row?.status === 'PENDING' && 'blue') ||
                                                            (row?.status === 'INACTIVE' && theme.palette.warning.dark)
                                                    }}
                                                >
                                                    {row?.status}
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="center"> {row?.minAge} </TableCell>
                                            <TableCell align="center"> {row?.maxAge} </TableCell>

                                            <TableCell align="right">
                                                {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                                    <Tooltip title="Update Module">
                                                        <IconButton onClick={() => handleModule(row)}>
                                                            <EditIcon color="success" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                )}

                                                <Tooltip title="View Module Detail">
                                                    <IconButton onClick={() => handleViewFocusSkill(row?.id)}>
                                                        <VisibilityIcon color="primary" fontSize="small" style={{ cursor: 'pointer' }} />
                                                    </IconButton>
                                                </Tooltip>

                                                <Tooltip title="View Sessions">
                                                    <IconButton onClick={() => handleViewSession(row?.id)}>
                                                        <VisibilityIcon color="primary" fontSize="small" style={{ cursor: 'pointer' }} />
                                                    </IconButton>
                                                </Tooltip>

                                                <Tooltip title="View Batches">
                                                    <IconButton onClick={() => handleViewBatch(row?.id)}>
                                                        <GroupWorkIcon color="primary" fontSize="small" style={{ cursor: 'pointer' }} />
                                                    </IconButton>
                                                </Tooltip>
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[10, 15, 25]}
                        component="div"
                        count={allModules?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
                {openAddCourses && (
                    <AddCourse
                        openAddCourses={openAddCourses}
                        setOpenAddCourses={setOpenAddCourses}
                        isEditData={isCourseUpdateData}
                        focusSkillList={focusSkillList}
                        setFocusSkillList={setFocusSkillList}
                        skillSelected={skillSelected}
                    />
                )}
                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
            </MainCard>
        </>
    );
}
